export const getAllAds = () =>
  fetch('/api/ads', { credentials: 'include' }).then(res => res.json());

export const getAdById = id =>
  fetch(`/api/ads/${id}`, { credentials: 'include' }).then(res => res.json());

export const postAd = formData =>
  fetch('/api/ads', {
    method: 'POST',
    credentials: 'include',
    body: formData
  }).then(res => res.json());

export const putAd = (id, formData) =>
  fetch(`/api/ads/${id}`, {
    method: 'PUT',
    credentials: 'include',
    body: formData
  }).then(res => res.json());

export const deleteAd = async id => {
  const res = await fetch(`/api/ads/${id}`, {
    method: 'DELETE',
    credentials: 'include'
  });
  const data = await res.json();
  if (!res.ok) throw data;
  return data;
};

export const searchAds = phrase =>
  fetch(`/api/ads/search/${encodeURIComponent(phrase)}`, { credentials: 'include' })
    .then(res => res.json());
