import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams, useNavigate, Link } from 'react-router-dom';
import { Box, Typography, Button, CardMedia, Card, CardContent } from '@mui/material';
import Spinner from '../components/Spinner';
import { fetchAd, removeAd } from '../features/ads/adsSlice';

export default function AdDetails() {
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { current: ad, status, error } = useSelector(state => state.ads);
  const user = useSelector(state => state.auth.user);
  const isAuthenticated = Boolean(user);

  useEffect(() => {
    dispatch(fetchAd(id));
  }, [id, dispatch]);

  if (status === 'loading') return <Spinner />;
  if (status === 'failed') return <Typography>Error: {error}</Typography>;
  if (!ad) return <Typography>Ad not found.</Typography>;

  const handleDelete = async () => {
    try {
      await dispatch(removeAd(id)).unwrap();
      navigate('/', { replace: true });
    } catch (err) {
      alert('Delete failed: ' + (err.message || JSON.stringify(err)));
    }
  };

  return (
    <Box sx={{ maxWidth: 800, mx: 'auto', mt: 4 }}>
      <Button component={Link} to="/" variant="outlined" sx={{ mb: 2 }}>
        All Ads
      </Button>
      <Card>
        {ad.photo && (
          <CardMedia
            component="img"
            height="300"
            image={ad.photo.startsWith('http') ? ad.photo : `/uploads/${ad.photo}`}
            alt={ad.title}
            sx={{ objectFit: 'cover' }}
          />
        )}
        <CardContent>
          <Typography variant="h4" gutterBottom>
            {ad.title}
          </Typography>
          <Typography variant="body2" color="text.secondary" gutterBottom>
            Published: {new Date(ad.publishedAt).toLocaleString()}
          </Typography>
          <Typography variant="body1" sx={{ mt: 2 }}>
            <strong>Price:</strong> ${ad.price}
          </Typography>
          <Typography variant="body1" sx={{ mt: 1 }}>
            <strong>Location:</strong> {ad.location}
          </Typography>
          <Typography variant="body1" sx={{ mt: 2 }}>
            <strong>Description:</strong> {ad.description}
          </Typography>
          <Box sx={{ mt: 3 }}>
            <Typography><strong>Seller info:</strong> {ad.sellerInfo}</Typography>
          </Box>
          {isAuthenticated && (
            <Box sx={{ mt: 3, display: 'flex', gap: 1 }}>
              <Button variant="contained" component={Link} to={`/ads/${id}/edit`}>
                Edit
              </Button>
              <Button variant="outlined" color="error" onClick={handleDelete}>
                Delete
              </Button>
            </Box>
          )}
        </CardContent>
      </Card>
    </Box>
  );
}
