// src/pages/Register.js
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { Box, TextField, Button, Typography, Alert } from '@mui/material';
import Spinner from '../components/Spinner';
import { register, clearError } from '../features/auth/authSlice';

const MAX_AVATAR_SIZE = 4 * 1024 * 1024;
const ALLOWED_TYPES = ['image/jpeg', 'image/png', 'image/gif'];

export default function Register() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { status, error: serverError } = useSelector(state => state.auth);

  useEffect(() => {
    dispatch(clearError());
  }, [dispatch]);

  const [form, setForm] = useState({ login: '', password: '', phone: '' });
  const [file, setFile] = useState(null);
  const [errors, setErrors] = useState({});
  const [success, setSuccess] = useState(false);

  const handleChange = e => {
    setForm(prev => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleFile = e => {
    const f = e.target.files[0];
    setFile(f);
    setErrors(prev => ({ ...prev, avatar: '' }));
  };

  const validate = () => {
    const errs = {};
    if (!form.login.trim()) errs.login = 'Login is required';
    if (!form.password) errs.password = 'Password is required';
    else if (form.password.length < 6) errs.password = 'Password must be at least 6 characters long';
    if (!form.phone.trim()) errs.phone = 'Phone is required';
    if (!file) errs.avatar = 'Avatar is required';
    else if (!ALLOWED_TYPES.includes(file.type)) errs.avatar = 'Allowed formats: jpg, jpeg, png, gif';
    else if (file.size > MAX_AVATAR_SIZE) errs.avatar = 'Avatar must be smaller than 4 MB';
    return errs;
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const errs = validate();
    if (Object.keys(errs).length) {
      setErrors(errs);
      return;
    }
    setErrors({});
    const fd = new FormData();
    fd.append('login', form.login);
    fd.append('password', form.password);
    fd.append('phone', form.phone);
    fd.append('avatar', file);
    try {
      await dispatch(register(fd)).unwrap();
      setSuccess(true);
      setTimeout(() => navigate('/login'), 2000);
    } catch {
      setSuccess(false);
    }
  };

  if (status === 'loading') return <Spinner />;

  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{ maxWidth: 360, mx: 'auto', mt: 5, display: 'grid', gap: 2 }}
    >
      <Typography variant="h5">Sign Up</Typography>

      {serverError && <Alert severity="error">{serverError}</Alert>}
      {success && <Alert severity="success">Registration successful! Redirecting to login…</Alert>}

      <TextField
        label="Login"
        name="login"
        value={form.login}
        onChange={handleChange}
        required
        error={Boolean(errors.login)}
        helperText={errors.login}
      />

      <TextField
        label="Password"
        name="password"
        type="password"
        value={form.password}
        onChange={handleChange}
        required
        error={Boolean(errors.password)}
        helperText={errors.password}
      />

      <TextField
        label="Phone"
        name="phone"
        value={form.phone}
        onChange={handleChange}
        required
        error={Boolean(errors.phone)}
        helperText={errors.phone}
      />

      <Button variant="contained" component="label">
        Upload Avatar *
        <input
          type="file"
          hidden
          accept="image/*"
          onChange={handleFile}
        />
      </Button>
      {file && <Typography>Selected file: {file.name}</Typography>}
      {errors.avatar && <Alert severity="error">{errors.avatar}</Alert>}
      <Typography variant="caption">
        Max size 4 MB. Allowed formats: png, jpeg, gif.
      </Typography>

      <Button type="submit" variant="contained">Sign Up</Button>
    </Box>
  );
}
