import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import {
  Box,
  TextField,
  Button,
  Typography
} from '@mui/material';
import Spinner from '../components/Spinner';
import { fetchAd, updateAd } from '../features/ads/adsSlice';

export default function EditAd() {
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { current: ad, status, error } = useSelector(state => state.ads);

  const [form, setForm] = useState({
    title: '',
    description: '',
    price: '',
    location: ''
  });
  const [file, setFile] = useState(null);

  useEffect(() => {
    dispatch(fetchAd(id));
  }, [dispatch, id]);

  useEffect(() => {
    if (ad) {
      setForm({
        title: ad.title,
        description: ad.description,
        price: ad.price,
        location: ad.location
      });
    }
  }, [ad]);

  const handleChange = e => {
    setForm(prev => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleFile = e => {
    setFile(e.target.files[0]);
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const fd = new FormData();
    Object.entries(form).forEach(([k, v]) => fd.append(k, v));
    if (file) fd.append('photo', file);

    const resultAction = await dispatch(updateAd({ id, formData: fd }));
    if (updateAd.fulfilled.match(resultAction)) {
      navigate(`/ads/${id}`, { replace: true });
    }
  };

  if (status === 'loading') return <Spinner />;
  if (status === 'failed')  return <p>Error: {error}</p>;
  if (!ad) return <p>Ad not found.</p>;

  return (
    <Box component="form" onSubmit={handleSubmit} sx={{ display: 'grid', gap: 2 }}>
      <Typography variant="h5">Edit ad</Typography>
      <TextField
        label="Title"
        name="title"
        required
        value={form.title}
        onChange={handleChange}
      />
      <TextField
        label="Description"
        name="description"
        multiline
        rows={4}
        required
        value={form.description}
        onChange={handleChange}
      />
      <TextField
        label="Price"
        name="price"
        type="number"
        required
        value={form.price}
        onChange={handleChange}
      />
      <TextField
        label="Location"
        name="location"
        required
        value={form.location}
        onChange={handleChange}
      />
      <Button variant="contained" component="label">
        Upload photo
        <input type="file" hidden onChange={handleFile} />
      </Button>
      <Button type="submit" variant="contained">
        Save changes
      </Button>
    </Box>
  );
}
