// src/pages/CreateAd.js
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  TextField,
  Button,
  Typography,
  Alert,
} from '@mui/material';
import Spinner from '../components/Spinner';
import { createAd } from '../features/ads/adsSlice';

const MAX_FILE_SIZE = 4 * 1024 * 1024;
const ALLOWED_TYPES = ['image/png', 'image/gif', 'image/jpeg'];

export default function CreateAd() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [form, setForm] = useState({
    title: '',
    description: '',
    price: '',
    location: '',
    sellerInfo: '',
  });
  const [file, setFile] = useState(null);
  const [fileError, setFileError] = useState('');
  const [status, setStatus] = useState('idle');
  const [errors, setErrors] = useState({});

  const validate = () => {
    const errs = {};
    if (form.title.trim().length < 10 || form.title.trim().length > 50) {
      errs.title = 'Title must be between 10 and 50 characters.';
    }
    if (form.description.trim().length < 20 || form.description.trim().length > 1000) {
      errs.description = 'Description must be between 20 and 1000 characters.';
    }
    const priceNum = Number(form.price);
    if (isNaN(priceNum)) {
      errs.price = 'Price must be a number.';
    } else if (priceNum < 0) {
      errs.price = 'Price must be at least 0.';
    }
    if (!form.location.trim()) {
      errs.location = 'Location is required.';
    }
    if (!form.sellerInfo.trim()) {
      errs.sellerInfo = 'Seller info is required.';
    }
    if (!file) {
      errs.photo = 'Photo is required.';
    }
    return errs;
  };

  const handleChange = e => {
    setForm(prev => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleFile = e => {
    const f = e.target.files[0];
    if (!f) return;
    if (!ALLOWED_TYPES.includes(f.type)) {
      setFile(null);
      setFileError('Only PNG, JPEG or GIF files are allowed.');
      return;
    }
    if (f.size > MAX_FILE_SIZE) {
      setFile(null);
      setFileError('The file size can be max. 4 MB.');
      return;
    }
    setFileError('');
    setFile(f);
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const errs = validate();
    if (Object.keys(errs).length > 0) {
      setErrors(errs);
      return;
    }
    setErrors({});
    setStatus('loading');

    const fd = new FormData();
    Object.entries(form).forEach(([k, v]) => fd.append(k, v));
    fd.append('photo', file);

    const result = await dispatch(createAd(fd));
    if (createAd.rejected.match(result)) {
      setStatus('idle');
      alert('Server error: ' + (result.error.message || 'Unknown error'));
      return;
    }

    setStatus('succeeded');
    navigate('/');
  };

  if (status === 'loading') {
    return <Spinner />;
  }

  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{ maxWidth: 600, mx: 'auto', mt: 4, display: 'grid', gap: 2 }}
    >
      <Typography variant="h5">Create new ad</Typography>

      {errors.photo && <Alert severity="error">{errors.photo}</Alert>}

      <TextField
        label="Title"
        name="title"
        value={form.title}
        onChange={handleChange}
        required
        error={Boolean(errors.title)}
        helperText={errors.title}
      />

      <TextField
        label="Description"
        name="description"
        multiline
        rows={4}
        value={form.description}
        onChange={handleChange}
        required
        error={Boolean(errors.description)}
        helperText={errors.description}
      />

      <TextField
        label="Price"
        name="price"
        type="number"
        value={form.price}
        onChange={handleChange}
        required
        error={Boolean(errors.price)}
        helperText={errors.price}
      />

      <TextField
        label="Location"
        name="location"
        value={form.location}
        onChange={handleChange}
        required
        error={Boolean(errors.location)}
        helperText={errors.location}
      />

      <TextField
        label="Seller Info"
        name="sellerInfo"
        value={form.sellerInfo}
        onChange={handleChange}
        required
        error={Boolean(errors.sellerInfo)}
        helperText={errors.sellerInfo}
      />

      <Button variant="contained" component="label">
        Upload Photo *
        <input
          type="file"
          hidden
          accept=".jpg,.jpeg,.png"
          onChange={handleFile}
        />
      </Button>
      {file && (
        <Typography variant="body2">
          Selected file: {file.name}
        </Typography>
      )}
      <Typography variant="caption" color="text.secondary">
        Max size 4 MB. Allowed formats: png, jpeg, gif.
      </Typography>
      {fileError && <Alert severity="error">{fileError}</Alert>}

      <Button type="submit" variant="contained">
        Submit
      </Button>
    </Box>
  );
}
