import React from 'react';
import { AppBar, Toolbar, Typography, Box, Button, Avatar } from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { logout } from '../features/auth/authSlice';

const API_BASE = process.env.REACT_APP_API_URL || 'http://localhost:8000';

export default function Header() {
  const user = useSelector(state => state.auth.user);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleLogout = async () => {
    await dispatch(logout());
    navigate('/', { replace: true });
  };

  const avatarSrc = user?.avatar
  ? user.avatar.startsWith('http')
    ? user.avatar
    : `${API_BASE}/uploads/${user.avatar.replace(/^\/+/, '')}`
  : null;

  return (
    <AppBar position="static">
      <Toolbar>
        <Typography
          variant="h6"
          component={Link}
          to="/"
          sx={{ flexGrow: 1, textDecoration: 'none', color: 'inherit' }}
        >
          Home
        </Typography>
        {user ? (
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Button component={Link} to="/ads/new" color="inherit">New Ad</Button>
            <Typography>{user.login}</Typography>
            {avatarSrc && <Avatar src={avatarSrc} alt={user.login}/>}
            <Button color="inherit" onClick={handleLogout}>Sign Out</Button>
          </Box>
        ) : (
          <Box>
            <Button component={Link} to="/login" color="inherit">Sign In</Button>
            <Button component={Link} to="/register" color="inherit">Sign Up</Button>
          </Box>
        )}
      </Toolbar>
    </AppBar>
  );
}