// src/pages/Login.js
import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { Box, TextField, Button, Typography, Alert } from '@mui/material'
import Spinner from '../components/Spinner'
import { login, clearError } from '../features/auth/authSlice'

export default function Login() {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const { user, status, error } = useSelector(state => state.auth)
  const [creds, setCreds] = useState({ login: '', password: '' })

  // przy pierwszym mount zostawiamy czysty error
  useEffect(() => {
    dispatch(clearError())
  }, [dispatch])

  // jeśli już zalogowany, redirect
  useEffect(() => {
    if (user) navigate('/', { replace: true })
  }, [user, navigate])

  const handleChange = e => {
    setCreds(prev => ({ ...prev, [e.target.name]: e.target.value }))
  }

  const handleSubmit = e => {
    e.preventDefault()
    dispatch(login(creds))
  }

  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{ maxWidth: 360, mx: 'auto', mt: 5, display: 'grid', gap: 2 }}
    >
      <Typography variant="h5">Sign in</Typography>
      {status === 'loading' && <Spinner />}
      {error && <Alert severity="error">{error}</Alert>}
      <TextField label="Login" name="login" required onChange={handleChange} />
      <TextField label="Password" name="password" type="password" required onChange={handleChange} />
      <Button type="submit" variant="contained">Sign in</Button>
    </Box>
  )
}
