// src/features/ads/adsSlice.js
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import * as adsApi from '../../api/adsApi';

export const fetchAds = createAsyncThunk('ads/fetchAds', async () => {
  return await adsApi.getAllAds();
});

export const fetchAd = createAsyncThunk('ads/fetchAd', async (id) => {
  return await adsApi.getAdById(id);
});

export const createAd = createAsyncThunk('ads/createAd', async (formData) => {
  return await adsApi.postAd(formData);
});

export const updateAd = createAsyncThunk('ads/updateAd', async ({id, formData}) => {
  return await adsApi.putAd(id, formData);
});

export const removeAd = createAsyncThunk('ads/removeAd', async (id) => {
  await adsApi.deleteAd(id);
  return id;
});

export const searchAds = createAsyncThunk('ads/searchAds', async (phrase) => {
  return await adsApi.searchAds(phrase);
});

const adsSlice = createSlice({
  name: 'ads',
  initialState: {
    items: [],
    current: null,
    searchResults: [],
    status: 'idle',
    error: null
  },
  reducers: {
    clearCurrent(state) {
      state.current = null;
    }
  },
  extraReducers: builder => {
    builder
      // fetchAds
      .addCase(fetchAds.pending, state => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(fetchAds.fulfilled, (state, { payload }) => {
        state.status = 'succeeded';
        state.items = payload;
      })
      .addCase(fetchAds.rejected, (state, { error }) => {
        state.status = 'failed';
        state.error = error.message;
      })
      // fetchAd
      .addCase(fetchAd.pending, state => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(fetchAd.fulfilled, (state, { payload }) => {
        state.status = 'succeeded';
        state.current = payload;
      })
      .addCase(fetchAd.rejected, (state, { error }) => {
        state.status = 'failed';
        state.error = error.message;
      })
      // createAd
      .addCase(createAd.pending, state => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(createAd.fulfilled, (state, { payload }) => {
        state.status = 'succeeded';
        state.items.unshift(payload);
      })
      .addCase(createAd.rejected, (state, { error }) => {
        state.status = 'failed';
        state.error = error.message;
      })
      // updateAd
      .addCase(updateAd.pending, state => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(updateAd.fulfilled, (state, { payload }) => {
        state.status = 'succeeded';
        state.items = state.items.map(ad =>
          ad._id === payload._id ? payload : ad
        );
        if (state.current && state.current._id === payload._id) {
          state.current = payload;
        }
      })
      .addCase(updateAd.rejected, (state, { error }) => {
        state.status = 'failed';
        state.error = error.message;
      })
      // removeAd
      .addCase(removeAd.pending, state => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(removeAd.fulfilled, (state, { payload }) => {
        state.status = 'succeeded';
        state.items = state.items.filter(ad => ad._id !== payload);
        if (state.current && state.current._id === payload) {
          state.current = null;
        }
      })
      .addCase(removeAd.rejected, (state, { error }) => {
        state.status = 'failed';
        state.error = error.message;
      })
      // searchAds
      .addCase(searchAds.pending, state => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(searchAds.fulfilled, (state, { payload }) => {
        state.status = 'succeeded';
        state.searchResults = payload;
      })
      .addCase(searchAds.rejected, (state, { error }) => {
        state.status = 'failed';
        state.error = error.message;
      });
  }
});

export const { clearCurrent } = adsSlice.actions;
export default adsSlice.reducer;
