import React from 'react';
import Grid from '@mui/material/Grid';
import Box  from '@mui/material/Box';
import AdCard from './AdCard';

export default function AdGrid({ ads }) {
  return (
    <Box sx={{ px: 2 }}>
      <Grid container spacing={2}>
        {ads.map(ad => (
          <Grid
            key={ad._id}
            size={{ xs: 12, sm: 6, md: 4 }} 
          >
            <AdCard ad={ad} />
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}
